<template>
  <div class="publishProducts">
    <div class="publishProductsHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="skip" />
        <span>{{ $t("index.text79") }}</span>
      </div>
    </div>
    <div class="slot"></div>

    <div class="publishProductsMain">
      <div class="productInfo">
        <div class="productName">{{ $t("index.text90") }}</div>

        <van-field v-model="productName" :placeholder="$t('index.text91')" />

        <div class="productName mt2r">{{ $t("index.text92") }}</div>

        <van-uploader :after-read="afterRead">
          <img
            src="../../assets/img/pic15.png"
            class="pic15"
            alt=""
            v-if="!content"
          />
          <img :src="content" class="pic15" alt="" v-else />
        </van-uploader>

        <div class="tips">{{ $t("index.text93") }}750PX.750PX</div>
      </div>

      <div class="setUp mt2r">
        <van-field
          v-model="shipping"
          :label="$t('index.text94')"
          :placeholder="$t('index.text95')"
        />
        <van-field
          v-model="price"
          :label="$t('index.text96')"
          :placeholder="$t('index.text95')"
        />
        <van-field
          v-model="contact"
          :label="$t('index.text171')"
          :placeholder="$t('index.text95')"
        />
      </div>

      <textarea
        v-model="description"
        :placeholder="$t('index.text97')"
        class="productInfoTextarea mt2r"
        rows="7"
      ></textarea>

      <div class="ok mt2r jcc" @click="publishProducts">
        {{ $t("index.text10") }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      showPaymentPopup: false,
      selectQuantity: 1,
      productName: "",
      description: "",
      price: "",
      content: "",
      shipping: "",
      productImage: "",
      contact: "",
      productInfo: {
        productId: ""
      },

      country: "CN"
    };
  },
  async activated() {
    this.productInfo = JSON.parse(this.$route.query.productInfo);
    if (this.productInfo.productId) this.getEditPoductInfo();
  },
  methods: {
    skip() {
      if (this.productInfo.status) {
        this.$router.push("/merchantCenter");
      } else {
        this.$router.push("/productManagement");
      }
      this.reset();
    },
    getEditPoductInfo() {
      this.$api
        .get("product/edit", {
          id: this.productInfo.productId
        })
        .then(res => {
          this.productName = res.data.title;
          this.description = res.data.description;
          this.price = res.data.price;
          this.shipping = res.data.shipping;
          this.productImage = res.data.image;
          this.content = res.data.full_url;
          this.contact = res.data.contact;
        });
    },
    afterRead(file) {
      this.content = file.content;
      let formdData = new FormData();
      formdData.append("file", file.file);
      this.$api.upload("upload/images", formdData).then(res => {
        if (res.code != 200) {
          this.$toast(res.msg);
          return;
        } else {
          this.productImage = res.data.url;
        }
      });
    },
    publishProducts() {
      if (!this.productName) {
        this.$toast(this.$t("index.text91"));
        return;
      }

      if (!this.productImage) {
        this.$toast(this.$t("index.text98"));
        return;
      }

      if (!this.shipping) {
        this.$toast(this.$t("index.text99"));
        return;
      }

      if (!this.price) {
        this.$toast(this.$t("index.text100"));
        return;
      }

      if (!this.contact) {
        this.$toast(this.$t("index.text172"));
        return;
      }
      if (!this.description) {
        this.$toast(this.$t("index.text97"));
        return;
      }
      if (this.productInfo.status) {
        this.add();
      } else {
        this.edit();
      }
    },
    reset() {
      this.productName = "";
      this.description = "";
      this.price = "";
      this.shipping = "";
      this.productImage = "";
      this.content = "";
    },
    add() {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text101"),
        className: "loading"
      });

      this.$api
        .post("product/add", {
          title: this.productName,
          description: this.description,
          price: this.price,
          shipping: this.shipping,
          image: this.productImage,
          country: this.country,
          contact: this.contact
        })
        .then(res => {
          if (res.code == 200) {
            this.$toast(this.$t("index.text102"));
          } else {
            this.$toast(res.msg);
          }
          this.reset();
        });
    },
    edit() {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text101"),
        className: "loading"
      });

      this.$api
        .post("product/edit", {
          id: this.productInfo.productId,
          title: this.productName,
          description: this.description,
          price: this.price,
          shipping: this.shipping,
          image: this.productImage,
          country: this.country,
          contact: this.contact
        })
        .then(res => {
          if (res.code == 200) {
            this.$toast(this.$t("index.text103"));
          } else {
            this.$toast(res.msg);
          }
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.publishProducts {
  height: 100%;
  overflow: scroll;
  background: #ffffff;
}
.publishProductsHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }
}

.publishProductsMain {
  padding: 2rem 2rem 4rem 2rem;

  .productInfo {
    background: #f7f7f7;
    border-radius: 0.4rem;
    padding: 1.5rem;
    .productName {
      font-weight: bold;
      font-size: 1.6rem;
    }
    .van-field {
      margin-top: 1.4rem;
      border-bottom: 0.1rem solid #cdcdcd;
      background: #f7f7f7;
      padding-left: 0;
      padding-right: 0;
      .van-field__control {
        color: #000;
      }
      /deep/ .van-field__control::placeholder {
        color: #000;
      }
    }
    .pic15 {
      margin-top: 1rem;
      width: 7rem;
      height: 7rem;
    }
    .tips {
      margin-top: 1rem;
    }
  }

  .setUp {
    background: #f7f7f7;
    border-radius: 0.4rem;
    padding: 2rem 1.5rem;
    /deep/ .van-field {
      border-bottom: 0.1rem solid #cdcdcd;
      background: #f7f7f7;
      padding-left: 0;
      padding-right: 0;
      height: 5rem;
      .van-field__label {
        width: auto;
        display: flex;
        align-items: center;
        span {
          font-weight: bold;
        }
      }
      .van-field__control {
        color: #666;
        text-align: right;
      }
      .van-field__control::placeholder {
        color: #666;
        text-align: right;
      }
    }
  }

  .productInfoTextarea {
    width: 100%;
    border: 0.1rem solid #999999;
    padding: 1.4rem;
    box-sizing: border-box;
    border-radius: 0.4rem;
    color: #000;
  }
  .productInfoTextarea::placeholder {
    color: #000;
  }
  .ok {
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    color: #fff;
    font-size: 1.6rem;
  }
}
</style>



